@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap");
@keyframes light {
  0%, 10%, 100% {
    box-shadow: 0 0 4px 2px #a405c6;
  }
  5% {
    box-shadow: 0 0 12px 8px #a405c6;
  }
}
@keyframes lightTube {
  100%, 0% {
    box-shadow: 0 0 60px 0px #a405c6;
  }
  50% {
    box-shadow: 0 0 120px 10px #a405c6;
  }
}
@keyframes neonText {
  100%, 0% {
    text-shadow: 0 0 2px #fff, 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #a405c6, 0 0 40px #a405c6, 0 0 45px #a405c6, 0 0 50px #a405c6, 0 0 75px #a405c6;
  }
  50% {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #fff, 0 0 40px #a405c6, 0 0 80px #a405c6, 0 0 90px #a405c6, 0 0 100px #a405c6, 0 0 150px #a405c6;
  }
}
html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Press Start 2P", cursive;
  background: transparent;
}

body.background {
  background: #0f0c1e;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 1920px;
  height: 1080px;
}

.overlay__viewers {
  flex: 0 0 8rem;
}

.overlay__last-follower {
  flex: 1 1 auto;
}

.overlay__last-sub {
  flex: 1 1 auto;
}

.overlay__followers-goal {
  flex: 0 0 28.8rem;
}

.overlay__electricity {
  position: absolute;
  left: 256px;
  top: 0;
  z-index: 2;
}

.overlay__roach {
  position: absolute;
  top: -50px;
  left: 0px;
}

.overlay__bigtube {
  position: absolute;
  z-index: 2;
  top: -92px;
  right: 32px;
  animation: lightTube 3s infinite;
}

.overlay__leftbar {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 224px;
  top: 32px;
  left: 12px;
  bottom: 80px;
}

.overlay__box {
  position: relative;
  background: transparent;
  color: #000;
  border: 4px solid;
  box-shadow: inset 0 -8px #000, -2px -2px #000, -4px -4px #000, -6px -6px #000, -8px -8px #000, -10px -10px #000, -12px -12px #000, -14px -14px #000;
}

.overlay__box:not([class*=screen]) {
  background: url("../images/crt-effect.png");
}

.overlay__box:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 4px;
  height: 4px;
  width: 4px;
  background: #fff;
  border: 2px solid rgba(164, 5, 198, 0.4);
  animation: light 10s ease infinite;
}

.overlay__box:not(:first-child):after {
  content: url("../images/tube-labo-overlay.gif");
  position: absolute;
  top: -4px;
  left: -36px;
  transform: rotate(-90deg);
}

[class*=leftbar] .overlay__box:after {
  content: url("../images/tube-labo-overlay.gif");
  position: absolute;
  top: auto;
  z-index: 1;
  bottom: -37px;
  left: 32px;
  transform: rotate(0) scaleX(-1);
}

.intro .overlay__box[class*=screen]::after {
  transform: rotate(0) scaleX(-1);
  top: auto;
  bottom: -40px;
  left: 20px;
  z-index: 2;
}

.overlay__screen {
  position: absolute;
  height: 920px;
  right: 4px;
  left: 268px;
  bottom: 80px;
}

.fullcam .overlay__screen {
  right: 60px;
}

.fullscreen-cam .overlay__screen {
  position: absolute;
  right: auto;
  left: 14px;
  bottom: -12px;
  width: calc(100% - 18px);
  height: 1040px;
}

.intro .overlay__screen {
  right: auto;
  left: 50%;
  transform: translateX(-50%);
  width: 1648px;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.intro .overlay__screen h2 {
  margin-top: 2rem;
}

.intro .overlay__screen h1,
.intro .overlay__screen h2 {
  color: #fff;
  animation: neonText 3s infinite;
}

.overlay__chat {
  flex: 1 1 auto;
}

.overlay__chat:not(:last-child) {
  margin-bottom: 32px;
}

.overlay__cam {
  width: 100%;
  height: 0;
  padding-bottom: 80%;
}

.fullscreen-cam .overlay__cam {
  position: absolute;
  bottom: 80px;
  right: 4px;
  padding-bottom: 0;
  width: 200px;
  height: 170px;
  z-index: 2;
}

.fullscreen-cam .overlay__cam::after {
  transform: rotate(0) scaleX(-1);
  top: auto;
  bottom: -37px;
  left: auto;
  right: 20px;
  z-index: 2;
}

.overlay__bottombar {
  position: absolute;
  display: flex;
  bottom: 0;
  left: 12px;
  right: 4px;
  height: 48px;
}

.overlay__bottombar > *:not(:last-child) {
  margin-right: 32px;
}